<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div class="menu_list"
         v-if="$route.name === 'menu_manage'">
      <mrc_ess_addBtn @handleAdd='handleAdd'></mrc_ess_addBtn>
      <v-card style="text-align: left">
        <v-card-subtitle style="font-size: 19px;color: #ffad4d">Applicant Tracking System</v-card-subtitle>
        <v-card-subtitle>
          <div class="menu_row">
            <div class="menu_name">{{$t('label_The_menu')}}</div>
            <div class="func_name">{{$t('label_The_name_of_the_function')}}</div>
            <div class="is_show">{{$t('label_display')}}</div>
          </div>
        </v-card-subtitle>
        <v-card-text>
          <menu-list-manage v-for="(item,index) in menuList"
                            :key='index'
                            :obj='item'
                            @delData='delData'
                            :level='0'></menu-list-manage>
        </v-card-text>
      </v-card>
    </div>
    <router-view v-else></router-view>
  </mrc_ess_skLoaders>
</template>

<script>
import menuListManage from './menu_list';
import { get_menuList } from '@/service/api/menu'
export default {
  components: { menuListManage },
  props: {},
  data () {
    return {
      menuList: [],
      skLoading: true
    };
  },
  created () { },
  async mounted () {
    this.skLoading = true
    if (this.$route.name == 'menu_manage') {
      const menu = await get_menuList()
      if (menu.code == 200) {
        this.menuList = menu.data
      }
    }
    this.skLoading = false
  },
  methods: {
    handleAdd () {
      this.$router.push({
        name: 'menu_add'
      })
    },
    delData (uuid) {
      this.Recursion(this.menuList, uuid)
    },
    Recursion (arr, uuid) {
      if (arr.length <= 0) return ''
      arr.forEach((item, index) => {
        if (item.uuid == uuid) {
          arr.splice(index, 1)
        } else {
          if (item.children) {
            this.Recursion(item.children, uuid)
          }

        }
      })
    }
  },
  computed: {},
  watch: {
    async '$route' (val) {
      if (val.name === 'menu_manage') {
        this.skLoading = true
        const menu = await get_menuList()
        if (menu.code == 200) {
          this.menuList = menu.data
        }
        this.skLoading = false
      }
    }
  },
};
</script>

<style scoped>
.menu_row {
  display: flex;
}
.menu_name {
  width: 38vw;
  padding-left: 20px;
}
.func_name {
  width: 19vw;
}
.is_show {
  width: 5vw;
}
</style>

